import * as React from "react";
import { useState, useEffect, useRef } from "react";
import {
  Search, Settings, LogOut, Database, Table2, Plus, Sun, Moon, Menu, X,
} from "lucide-react";

const data = [
  { id: "evt_01", event: "Launch Party", eventId: "A-1001", paid: true, used: "2025-10-01 19:43" },
  { id: "evt_02", event: "VIP Dinner", eventId: "B-2099", paid: false, used: null },
  { id: "evt_03", event: "Hack Night", eventId: "C-7782", paid: true, used: null },
];

const MiniStudio: React.FC = () => {
  const [query, setQuery] = useState("");
  const [theme, setTheme] = useState<"light" | "dark">("light");
  const [open, setOpen] = useState(false);
  const [navOpen, setNavOpen] = useState(false);
  const [page, setPage] = useState<"dashboard" | "test">("dashboard");
  const menuRef = useRef<HTMLDivElement>(null);

  const filtered = data.filter(
    (r) =>
      r.id.includes(query) ||
      r.event.toLowerCase().includes(query.toLowerCase()) ||
      r.eventId.includes(query)
  );

  useEffect(() => {
    function handleClick(e: MouseEvent) {
      if (menuRef.current && !menuRef.current.contains(e.target as Node)) setOpen(false);
    }
    document.addEventListener("click", handleClick);
    return () => document.removeEventListener("click", handleClick);
  }, []);

  return (
    <div className={`app ${theme}`}>
      <style>{`
        body, html { margin: 0; font-family: 'Inter', sans-serif; }
        .app { min-height: 100vh; background: var(--bg); color: var(--text); transition: background .3s, color .3s; overflow-x: hidden;}
        .app.light {
          --bg: #f8fafc; --panel: #ffffff; --border: rgba(0,0,0,0.08);
          --text: #111827; --muted: #6b7280; --accent: #10b981; --accent-bg: rgba(16,185,129,0.1);
        }
        .app.dark {
          --bg: #0f1318; --panel: #0b0f14; --border: rgba(255,255,255,0.08);
          --text: #d1d5db; --muted: #9ca3af; --accent: #10b981; --accent-bg: rgba(16,185,129,0.15);
        }
        header {
          background: var(--panel); border-bottom: 1px solid var(--border);
          padding: 12px 20px; display: flex; align-items: center; justify-content: space-between;
          position: sticky; top: 0; z-index: 30;
        }
        .brand { display: flex; align-items: center; gap: 8px; font-weight: 600; color: var(--text); font-size: 14px;}
        .brand-icon { width: 24px; height: 24px; background: linear-gradient(135deg, #10b981, #059669); border-radius: 3px;}
        .actions { display: flex; align-items: center; gap: 10px;}
        .search-box { position: relative; }
        .search-box input {
          background: var(--panel); border: 1px solid var(--border);
          border-radius: 4px; color: var(--text); padding: 7px 8px 7px 30px; width: 200px; font-size: 13px; outline: none;
        }
        .search-icon { position: absolute; left: 8px; top: 50%; transform: translateY(-50%); opacity: .6; color: var(--muted);}
        button {
          border: none; border-radius: 5px; font-size: 13px; padding: 7px 12px; cursor: pointer;
          background: var(--panel); color: var(--text); display: flex; align-items: center; gap: 6px; transition: all .2s;
        }
        button:hover { background: var(--accent-bg); }
        .primary-btn { background: var(--accent); color: #072c21; font-weight: 500;}
        main { max-width: 1100px; margin: 30px auto; padding: 0 20px;}
        section {
          background: var(--panel); border: 1px solid var(--border);
          border-radius: 10px; overflow: hidden;
        }
        .table-header {
          display: flex; justify-content: space-between; align-items: center;
          padding: 12px 16px; border-bottom: 1px solid var(--border);
        }
        table { width: 100%; border-collapse: collapse; font-size: 13px;}
        th, td { text-align: left; padding: 8px 16px; border-bottom: 1px solid var(--border);}
        th { color: var(--muted); font-size: 11px; text-transform: uppercase;}
        tr:hover { background: var(--accent-bg);}
        .pill {
          display: inline-flex; align-items: center; gap: 5px; border-radius: 5px; padding: 2px 8px; font-size: 11px;
        }
        .pill.true { background: rgba(16,185,129,0.15); color: #10b981; }
        .pill.false { background: rgba(245,158,11,0.15); color: #f59e0b; }
        footer { text-align: center; color: var(--muted); font-size: 12px; margin: 40px 0 20px;}
        /* dropdown menu */
        .dropdown { position: relative; }
        .menu {
          position: absolute; top: 110%; right: 0; background: var(--panel);
          border: 1px solid var(--border); border-radius: 8px; box-shadow: 0 4px 10px rgba(0,0,0,0.2);
          width: 160px; overflow: hidden; z-index: 50; display: none;
        }
        .menu.open { display: block; }
        .menu button { width: 100%; justify-content: flex-start; background: none; border-radius: 0; }
        .menu button:hover { background: var(--accent-bg); }
        .menu hr { border: none; border-top: 1px solid var(--border); margin: 4px 0; }
        /* side nav overlay */
        .sidenav-overlay {
          position: fixed; top: 0; left: 0; height: 100%; width: ${navOpen ? "220px" : "0"};
          background: var(--panel); border-right: 1px solid var(--border);
          overflow: hidden; transition: width .25s ease-in-out; z-index: 40;
          display: flex; flex-direction: column; padding: ${navOpen ? "20px" : "0"};
        }
        .sidenav-overlay button {
          background: none; border: none; color: var(--text); text-align: left; padding: 8px; font-size: 14px; border-radius: 6px;
        }
        .sidenav-overlay button:hover { background: var(--accent-bg); }
        .nav-backdrop {
          position: fixed; inset: 0; background: rgba(0,0,0,0.4); z-index: 35;
          opacity: ${navOpen ? "1" : "0"}; pointer-events: ${navOpen ? "auto" : "none"}; transition: opacity .2s;
        }
      `}</style>

      {/* NAV OVERLAY */}
      <div className="sidenav-overlay">
        <button onClick={() => { setPage("dashboard"); setNavOpen(false); }}>
          🏠 Dashboard
        </button>
        <button onClick={() => { setPage("test"); setNavOpen(false); }}>
          🧪 Test
        </button>
      </div>
      <div className="nav-backdrop" onClick={() => setNavOpen(false)} />

      <header>
        <div className="brand">
          <button onClick={() => setNavOpen(!navOpen)} style={{ background: "none", border: "none", cursor: "pointer" }}>
            {navOpen ? <X size={18} /> : <Menu size={18} />}
          </button>
          <div className="brand-icon" />
          Mini Studio
        </div>
        <div className="actions">
          <div className="search-box">
            <Search className="search-icon" size={14} />
            <input value={query} onChange={(e) => setQuery(e.target.value)} placeholder="Search events…" />
          </div>

          {/* Settings dropdown */}
          <div className="dropdown" ref={menuRef}>
            <button onClick={() => setOpen(!open)}>
              <Settings size={14} /> Settings
            </button>
            <div className={`menu ${open ? "open" : ""}`}>
              <button onClick={() => alert("Profile clicked")}>Profile</button>
              <button onClick={() => alert("Preferences clicked")}>Preferences</button>
              <hr />
              <button onClick={() => setTheme(theme === "light" ? "dark" : "light")}>
                {theme === "light" ? <Moon size={14} /> : <Sun size={14} />} Theme:{" "}
                {theme === "light" ? "Dark" : "Light"}
              </button>
            </div>
          </div>

          <button className="primary-btn">
            <LogOut size={14} /> Sign out
          </button>
        </div>
      </header>

      <main>
        {page === "dashboard" ? (
          <section>
            <div className="table-header">
              <div>
                <strong>events</strong>
                <div style={{ fontSize: "11px", color: "var(--muted)" }}>Demo table with a few columns</div>
              </div>
              <button className="primary-btn">
                <Plus size={14} /> New row
              </button>
            </div>
            <table>
              <thead>
                <tr>
                  <th>ID</th><th>event</th><th>eventId</th><th>paid</th><th>used</th>
                </tr>
              </thead>
              <tbody>
                {filtered.map((row) => (
                  <tr key={row.id}>
                    <td style={{ fontFamily: "monospace", opacity: 0.8 }}>{row.id}</td>
                    <td>{row.event}</td>
                    <td style={{ fontFamily: "monospace", opacity: 0.8 }}>{row.eventId}</td>
                    <td>
                      <span className={`pill ${row.paid ? "true" : "false"}`}>
                        {row.paid ? "true" : "false"}
                      </span>
                    </td>
                    <td>{row.used ?? <span style={{ color: "var(--muted)" }}>null</span>}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </section>
        ) : (
          <section style={{ padding: "30px", textAlign: "center", fontSize: "18px" }}>
            🧪 Implement me...
          </section>
        )}
      </main>

      <footer>Built for demo purposes — Overlay nav + theme toggle.</footer>
    </div>
  );
};

export default MiniStudio;
